import {Link, useNavigate, useParams} from "react-router-dom";
import React, {useEffect, useState} from "react";
import './StoreDetail.css'
import Cookies from "js-cookie";

const StoreDetail = () => {
    const {productId} = useParams();
    const [product, setProduct] = useState(null);
    const [quantity, setQuantity] = useState(1);
    const navigate = useNavigate();
    useEffect(() => {
        const fetchProductData = async () => {
            try {
                const response = await fetch(`/api/products/detail/${productId}`);
                if (response.ok) {
                    const data = await response.json();
                    setProduct(data);
                } else {
                    console.error('Failed to fetch product data:', response.status);
                }
            } catch (error) {
                console.error('Error fetching product data:', error);
            }
        };

        fetchProductData();
    }, [productId]);

    if (!product) {
        return <div>Loading...</div>;
    }


    const handleGoBack = () => {
        navigate(-1); // -1   .
    };

    const handleIncreaseQuantity = () => {
        setQuantity(quantity + 1);
    };

    const handleDecreaseQuantity = () => {
        if (quantity > 1) {
            setQuantity(quantity - 1);
        }
    };

    const totalPrice = product.price * quantity;
    const handleButtonClick = async (action) => {
        try {
            const response = await fetch('/api/orders', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    productId,
                    quantity,
                }),
            });

            if (response.ok) {

                if (action === 'cart') {
                    navigate('/cart');
                } else if (action === 'purchase') {
                    navigate('/purchase');
                }

                setQuantity(1);

            } else {
                console.error('Failed to create/update order:', response.status);
            }
        } catch (error) {
            console.error('Error creating/updating order:', error);
        }
    }
    return (
        <div className="detail-main">
            <div className="detail-header">
                <div>{product.productName}</div>
                <img src="/images/arrow-left-solid.svg" alt="Back" onClick={handleGoBack}/>
            </div>
            <div className="detail-content">
                <div className="detail-img">
                    <img src={`/images/${product.imagePath}.jpg`} alt={product.name}/>
                </div>
                <div className="detail-text">
                    <div className="detail-price">{product.price.toLocaleString()}</div>
                    <div className="detail-info-box">
                        <div className="detail-info-title">
                            <dt className="dt-info-title"></dt>
                            <dt className="dt-info-title"></dt>
                            <dt className="dt-info-title"></dt>
                        </div>
                        <div className="detail-info-content">
                            <dd>{product.productDetail}</dd>
                            <dd> 6 </dd>
                            <dd> </dd>
                        </div>
                    </div>
                    <div className="quantityAndPrice">
                        <div className="quantity-controls">
                            <button className="minusBtn" onClick={handleDecreaseQuantity}>-</button>
                            <span>{quantity}</span>
                            <button className="plusBtn" onClick={handleIncreaseQuantity}>+</button>
                        </div>
                        <div className="qap-price">{totalPrice.toLocaleString()}</div>
                    </div>
                    <div className="totalPrice">
                         
                        <span>{totalPrice.toLocaleString()}</span>
                    </div>
                    <div className="btn-box">
                        <button className="cartBtn" onClick={() => handleButtonClick('cart')}>
                            
                        </button>
                        <button className="buyBtn" onClick={() => handleButtonClick('purchase')}>
                            
                        </button>
                    </div>
                </div>
            </div>
            <dl>
                <dt className="ddTitle"></dt>
                <dd className="ddText">
                    <p>         .<br/>
                                   .<br/>
                         <strong>   ,</strong><br/>
                              .<br/>
                        ( ''  ,                
                        .)<br/>
                                   .<br/>
                              .<br/>
                         <strong>   ,</strong><br/>
                          5       , 1   3(92)   .&nbsp;&nbsp;<br/>
                        ,           .<br/>
                            90%  .&nbsp;&nbsp;<br/>
                               CJ ONE  .<br/>
                              .</p>
                </dd>
                <dt className="ddTitle">/</dt>
                <dd className="ddText"><p>      100%  / ,      
                    90%    .<br/>
                     ,     /  .&nbsp;<br/>
                     / <br/>
                     App, &gt; MY &gt;  &gt;  or  &gt; My page &gt;  &gt;    
                    .<br/>
                    (  60   ,      ,   )<br/>
                     App, &gt; MY &gt;  or  &gt; My page &gt;  &gt;        
                    ,     .<br/>
                      ,       .<br/>
                           ,         .&nbsp;<br/>
                                .<br/>
                      1544-1122</p></dd>
            </dl>
        </div>
    );
};
export default StoreDetail;