// components/Reservation.js
import React, {useState, useEffect} from 'react';
import axios from 'axios';
import './Reservation.css';
import {useLocation, useParams} from 'react-router-dom';

const SeatSelection = ({seats, onSeatSelect, reservedSeats, selectedSeats}) => {
    const isSeatReserved = (seatNumber) => reservedSeats.includes(seatNumber);

    return (
        <div className="seat-selection-container">
            <label></label>
            <div className="seat-grid">
                {seats.map((seat) => {
                    const isReserved = isSeatReserved(seat.seatNumber);
                    const isSelected = selectedSeats.includes(seat.seatNumber);

                    return (
                        <button
                            key={seat.seatNumber}
                            onClick={() => !isReserved && onSeatSelect(seat.seatNumber)}
                            className={`seat ${isSelected ? 'selected' : ''} ${isReserved ? 'reserved' : ''}`}
                            disabled={isReserved}
                        >
                            <span className="seat-number">{isReserved ? 'X' : seat.seatNumber}</span>
                        </button>
                    );
                })}
            </div>
        </div>
    );
};

const Reservation = () => {
    const [movies, setMovies] = useState([]);
    const [selectedMovie, setSelectedMovie] = useState('');
    const [schedules, setSchedules] = useState([]);
    const [selectedScheduleId, setSelectedScheduleId] = useState(null);
    const [selectedSchedule, setSelectedSchedule] = useState(null);
    const [reservedSeats, setReservedSeats] = useState([]);
    const [selectedSeats, setSelectedSeats] = useState([]);
    const [selectedTheaterId, setSelectedTheaterId] = useState(null);
    const [theaters, setTheaters] = useState([]);
    const [seats, setSeats] = useState([]);
    const [selectedTheater, setSelectedTheater] = useState('');
    const [selectedMovieImage, setSelectedMovieImage] = useState(null);
    // useParams    theaterId 
    const {theaterId} = useParams();
    const location = useLocation();

    useEffect(() => {
        //     API 
        axios.get('/api/reservation/theaters')
            .then(response => setTheaters(response.data))
            .catch(error => console.error('Error fetching theaters:', error));
    }, []);
    useEffect(() => {
        // location theaterId 
        const theaterIdFromLocation = location.pathname.split('/reservation/')[1];
        setSelectedTheaterId(theaterId)
        // Id    
        if (theaterIdFromLocation) {
            const selectedTheater = theaters.find(theater => theater.theaterId === Number(theaterIdFromLocation));
            if (selectedTheater) {
                setSelectedTheater(selectedTheater.theaterName);
                //        API 
                axios.get(`/api/reservation/theaters/${theaterIdFromLocation}/movies`)
                    .then(response => setMovies(response.data))
                    .catch(error => console.error('Error fetching movies:', error));
            }
        }
    }, [location.pathname, theaters]);
    useEffect(() => {
        // theaterId, movieId, scheduleId     
        if (selectedScheduleId) {
            // Axios     API 
            axios.get(`/api/reservation/seats/${selectedTheaterId}/${selectedMovie}/${selectedScheduleId}`)
                .then(response => {
                    //      
                    const loadedSeats = response.data.seats;
                    const loadedReservedSeats = response.data.reservedSeats;

                    //      
                    const preselectedSeats = loadedSeats.filter(seat => loadedReservedSeats.includes(seat.seatNumber));
                    setSelectedSeats(preselectedSeats);

                    //     
                    const reservedSeatNumbers = loadedReservedSeats.map(seat => seat.seatNumber);
                    setReservedSeats(reservedSeatNumbers);

                    //       
                    setSeats(loadedSeats);
                })
                .catch(error => console.error('Error fetching seats:', error));
        }
    }, [selectedScheduleId]);
    useEffect(() => {
        setSelectedMovieImage(`/images/movie${selectedMovie}.jpg`);
    }, [selectedMovie, movies]);

    const handleTheaterChange = (e) => {
        const selectedTheaterName = e.target.value;
        const selectedTheater = theaters.find(theater => theater.theaterName === selectedTheaterName);
        //      id 
        const selectedTheaterId = selectedTheater ? selectedTheater.theaterId : null;
        setSelectedTheaterId(selectedTheaterId);
        //         API 
        if (selectedTheaterId) {
            axios.get(`/api/reservation/theaters/${selectedTheaterId}/movies`)
                .then(response => setMovies(response.data))
                .catch(error => console.error('Error fetching movies:', error));
        }

        //   
        setSelectedTheater(selectedTheaterName);
        //    
        setSelectedMovie(null);
        setSelectedSchedule(null);
        setSelectedSeats([]);
    };

    const handleMovieChange = (movieId) => {
        //        API 
        axios.get(`/api/reservation/theaters/${selectedTheaterId}/movies/${movieId}/schedules`)
            .then(response => setSchedules(response.data))
            .catch(error => console.error('Error fetching schedules:', error));

        setSelectedMovie(movieId);
        setSelectedSchedule(null);
        setSelectedSeats([]);
    };

    const handleScheduleChange = (e) => {
        setSelectedScheduleId(e);
        setSelectedSchedule(e);
        setSelectedSeats([]);
    };

    const handleSeatSelect = (seatNumber) => {
        //      
        if (selectedSeats.includes(seatNumber)) {
            setSelectedSeats(prevSeats => prevSeats.filter(seat => seat !== seatNumber));
        } else {
            //     
            setSelectedSeats(prevSeats => [...prevSeats, seatNumber]);
        }
    };
    const handleReservationSubmit = () => {
        const reservationData = {
            theaterId: selectedTheaterId,
            movieId: selectedMovie,
            scheduleId: selectedScheduleId,
            seats: selectedSeats,
        };

        axios.post('/api/reservation/reservations', reservationData)
            .then(response => {
                console.log(response.data);
                alert(' .');
                window.location.reload();
            })
            .catch(error => console.error('Error submitting reservation:', error));

        setSelectedTheater(null);
        setSelectedMovie(null);
        setSelectedSchedule(null);
        setSelectedSeats([]);
    };

    return (
        <div className="reservation-container">
            <h1> </h1>
            <div className="reservation-container-choice">
                <div>
                    <div className="select-container">
                        <label></label>
                        <select value={selectedTheater} onChange={handleTheaterChange}>
                            <option value="" disabled>
                                 .
                            </option>
                            {theaters.map((theater) => (
                                <option key={theater.theaterId} value={theater.theaterName}>
                                    {theater.theaterName}
                                </option>
                            ))}
                        </select>
                    </div>

                    <div className="select-container">
                        <label></label>
                        <select onChange={(e) => handleMovieChange(e.target.value)}>
                            <option value="" disabled selected>
                                 .
                            </option>
                            {movies.map((movie) => (
                                <option key={movie.movieId} value={movie.movieId}>
                                    {movie.title}
                                </option>
                            ))}
                        </select>
                    </div>


                    <div className="select-container">
                        <label></label>
                        <select onChange={(e) => handleScheduleChange(e.target.value)}>
                            <option value="" disabled selected>
                                 .
                            </option>
                            {schedules.map((schedule) => (
                                <option key={schedule.scheduleId} value={schedule.scheduleId}>
                                    {new Date(schedule.startTime).toLocaleString()}
                                </option>
                            ))}
                        </select>
                    </div>

                </div>
                {selectedMovie && (
                    <div className="reservation-img">
                        <img src={selectedMovieImage} alt="Selected Movie"/>
                    </div>
                )}
            </div>
            {selectedSchedule && (
                <SeatSelection
                    seats={seats}
                    onSeatSelect={handleSeatSelect}
                    reservedSeats={reservedSeats}
                    selectedSeats={selectedSeats}
                />
            )}
            {selectedSeats.length > 0 && (
                <div className="reservation-summary">
                    <p> : {selectedSeats.join(', ')}</p>
                    <button className="reservation-button" onClick={handleReservationSubmit}>
                        
                    </button>
                </div>
            )}
        </div>
    );
};

export default Reservation;