// Nav.js
import React, {useEffect, useState} from 'react';
import {Link, useLocation, useNavigate} from 'react-router-dom';
import axios from 'axios';
import Swal from 'sweetalert2';
import { useUserContext } from './components/User/UserContext';
const Nav = () => {
    const { isLoggedIn, setIsLoggedIn } = useUserContext();
    const location = useLocation();
    const navigate = useNavigate();
    useEffect(() => {
        //      
        checkLoginStatus();
    }, []);

    const checkLoginStatus = async () => {
        try {
            //  check-login     
            const response = await axios.get('/api/users/check-login');
            setIsLoggedIn(response.data);
        } catch (error) {
            console.error('   :', error.message);
        }
    };

    const handleLogout = async () => {
        try {
            //  
            await axios.post('/api/users/logout');
            await Swal.fire(' !');

            //  
            setIsLoggedIn(false);
            navigate('/');
        } catch (error) {
            console.error(' :', error.message);
        }
    };

    return (
        <nav className="navbar">
            <div className="navbar-content">
                <div className="nc1">
                    <div className="header-content">
                        <div className="logo">
                            <img className="logo" src="/images/logo.png" alt="" />
                        </div>
                        <div className="header-icons">
                            {isLoggedIn ? (
                                <a className="header-user" onClick={handleLogout}>
                                    <div>
                                        <img className="header-img" src="/images/user-logout.svg" alt=" " />
                                    </div>
                                    
                                </a>
                            ) : (
                                <>
                                    <Link to="/login" className="header-user">
                                        <div>
                                            <img className="header-img" src="/images/user-lock.svg" alt=" " />
                                        </div>
                                        
                                    </Link>
                                    <Link to="/signup" className="header-user">
                                        <div>
                                            <img className="header-img" src="/images/user-add.svg" alt=" " />
                                        </div>
                                        
                                    </Link>
                                </>
                            )}
                            <Link to="/mypage" className="header-user">
                                <div>
                                    <img className="header-img" src="/images/user-mypage.svg" alt=" " />
                                </div>
                                
                            </Link>
                            <Link to="/qna" className="header-user">
                                <div>
                                    <img className="header-img" src="/images/user-qna.svg" alt=" " />
                                </div>
                                
                            </Link>
                        </div>
                    </div>
                </div>
                <div className="nc2">
                    <div className="nav">
                        <Link to="/" className={`nav-item ${location.pathname === '/' ? 'active' : ''}`}>
                            
                        </Link>
                        <Link to="/movies" className={`nav-item ${location.pathname === '/movies' ? 'active' : ''}`}>
                            
                        </Link>
                        <Link to="/theater" className={`nav-item ${location.pathname === '/theater' ? 'active' : ''}`}>
                            
                        </Link>
                        <Link to="/reservation" className={`nav-item ${location.pathname.startsWith('/reservation') ? 'active' : ''}`}>
                            
                        </Link>
                        <Link to="/store" className={`nav-item ${location.pathname === '/store' ? 'active' : ''}`}>
                            
                        </Link>
                    </div>
                </div>
            </div>
        </nav>
    );
};

export default Nav;
